<template>
	<div>
		<HeaderComponent :view-title="headerTitle"></HeaderComponent>
		<ul id="helpContainer">
			<li>如果您在创建抽奖活动中有任何疑问，都可以联系客服为您解答！</li>
			<li>工作时间：周一到周六9:00~18:00</li>
			<li>联系QQ：123457</li>
			<li>微信公总号：huodongle123</li>
		</ul>
	</div>
</template>

<script type="text/javascript">
	import HeaderComponent from '../../components/header/header';
	export default {
		data(){
			return {
				headerTitle:{
					name:'联系客服',	//页面标题
					showCode:1
				}
			}
		},
		components:{
			HeaderComponent
		}
	}
</script>
<style lang="scss" scoped>
	@import '../../style/mixin';
	#helpContainer{
		width:100%;
		margin-top: 2.7rem;
		padding: 0 0.3rem;
		background:#fff;
		@include sc(0.8rem,#333);
		li{
			padding:0.5rem 0;
			&:not(:last-child){
				border-bottom: 1px solid #DCDCDC;
			}
		}
	}
</style>


